.HelpGeneral {
    width: 100%;
    display: flex;
    flex-direction: column;
  
    div {
        display: flex;
        flex-direction: column;
        margin-bottom: 8px;
    }
  
    h1 {
      display: flex;
      justify-content: space-between;
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 24px;
      color: #161616;
      padding: 24px;
      background: white;
    }
  
    p {
      padding: 24px;
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 22px;
      background: white;
      border-top: 1px solid rgba(22, 22, 22, 0.04);
      color: rgba(22, 22, 22, 0.74);
    }
  }
  
  @media screen and (max-width: 560px) {
    .Help-Cont-Wrap {
      flex-direction: column;
      align-items: center;
    }
  }